_v("1.2.4. Avtorizatsiya muvaffaqiyatli amalga oshirilganda, foydalanuvchiga tegishli xabar ko'rsatiladi va\n    \"Beeline\"ning Wi-Fi tarmog’iga kirishga ruxsat etiladi. Avtorizatsiya muvaffaqiyatsiz amalga oshirilgan hollarda,\n    foydalanuvchiga xatolar, uning sabablari va hal qilish usullari to’g’risidagi xabar ko’rsatiladi;\n  ")]),_c('p',[_vm._v("1.3. Xizmatga kirish vaqt va trafik hajmi bo’yicha cheklovlarsiz taqdim etiladi.\n  ")]),_c('p',[_vm._v("2. Xizmat ta'rifi\n  ")]),_c('p',[_vm._v("2.1. Ushbu xizmat Foydalanuvchilarga simsiz kirish tarmog'i orqali Internetga kirish imkonini beradi (keyingi\n    o’rinlarda – “Xizmatlar”). Xizmat faqatgina Foydalanuvchi \"Beeline\" Wi-Fi tarmog’ining ruxsat etilgan kirish\n    nuqtalarida radiosignal qat’iy qabul qilingan zonada joylashgan bo'lsa taqdim etiladi.\n  ")]),_c('p',[_vm._v("2.2. Foydalanuvchilarga xizmat ko’rsatish ushbu shartnoma shartlari bo'yicha taqdim etiladi.\n  ")]),_c('p',[_vm._v("2.3. Xizmatga kirish mobil telefon, shaxsiy planshetli kompyuterlar, noutbuklar va Wi-Fi texnologiyasi orqali\n    tarmoqqa ulanish mumkin bo’lgan boshqa qurilmalardan foydalangan holda amalga oshirilishi mumkin. Xizmatlardan\n    foydalanish uchun foydalanish (oxirgi) qurilmasini sozlash Foydalanuvchi tomonidan mustaqil amalga oshiriladi.\n  ")]),_c('p',[_vm._v("2.4. Xizmat zarur profilaktika va ta'mirlash ishlari o’tkazilishini mustasno qilganda, vaqt bo’yicha\n    cheklovlarsiz taqdim etiladi.\n  ")]),_c('p',[_vm._v("2.5. Xizmatlar 2,4 GHz, 5 GHz chastotalar bo'yicha taqdim etilishi mumkin. Muayyan joyda xizmatni taqdim etish\n    Operatorning texnik imkoniyatlari, shuningdek foydalanish (oxirgi) moslamaning texnik imkoniyatlariga bog'liq.\n  ")]),_c('p',[_vm._v("2.6. Xizmatlar 100 Mb / soniyali maksimal tezlikda taqdim etiladi. Tarmoq resurslaridan foydalanishning\n    intensivlik darajasi va bitta kirish nuqtasiga ulangan barcha Foydalanuvchilarning bir vaqtning o'zida o'tkazib\n    yuborilgan trafik hajmiga qarab, tezlik o'zgarishi mumkin. Operator foydalanish (oxirgi) qurilmasi va operatorning\n    aloqa tarmog'i va Xizmatlarga uzluksiz kirish o'rtasida axborot almashish tezligiga kafolat bermaydi.\n  ")]),_c('p',[_vm._v("2.7. Mobil telefonning faqat bitta raqami yordamida bitta foydalanish (oxirgi) qurilmasini avtorizatsiya qilish\n    mumkin.\n  ")]),_c('p',[_vm._v("2.8. Xizmatdan foydalanishda foydalanish (oxirgi) qurilmasiga dinamik, xususiy IP-manzillar tayinlanadi.\n  ")]),_c('p',[_vm._v("2.9. Xizmatlar, \"xuddi shunday\" ko’rinishda («as is») va taqdim etilgan paytda mavjud bo'lgan shaklda taqdim\n    etiladi, bunda Foydalanuvchilarga axborot almashinuv tezligi, Xizmat va boshqalarga uzluksiz kirishning hech\n    qanday, bevosita yoki bilvosita ko’rinishiga kafolat taqdim etilmaydi.\n  ")]),_c('p',[_vm._v("2.10. Operator, Qirg'iziston Respublikasi qonunchiligiga muvofiq tarqatish taqiqlangan axborotni o'z ichiga olgan\n    Internet-resurslar, shu jumladan, giyohvandlik vositalari, psixotrop moddalarni targ’ib qilish, organlar va (yoki)\n    inson to'qimalarini taklifl qilish, pornografik materiallar, bosma nashrlar, pornografik xarakterdagi tasvirlar\n    yoki boshqa predmetlar, odam savdosiga yashirin jabrlanuvchilarni jalb qilishga yo’naltirilgan ma’lumotlar, jinsiy\n    aloqa xarakteridagi xizmatlarga jalb etish, shuningdek, sud-huquqiy aktlar va/yoki sud qarorlari asosida\n    blokirovka qilingan axborotlardan tashqari har qanday Internet-resurslarga (tarmoqlar, serverlar, ma'lumotlar\n    saqlanadigan joy va boshqalar) kirish huquqini cheklamaydi.\n  ")]),_c('p',[_vm._v("2.11. Foydalanuvchi ushbu Shartnomaning shartlarini qabul qilar ekan, Operator Xizmat ko’rsatishni amalga\n    oshirayotganda Foydalanuvchining shaxsiy ma'lumotlarini qayta ishlash va Xizmatni ko'rsatish maqsadida ular bilan\n    tanishish huquqiga egaligi, shuningdek, Operator tomonidan Xizmatni taqdim qilish vaqtida Operatorga ma'lum\n    bo'lgan Foydalanuvchi haqidagi ma'lumotlarni to'plash va q