<template>
  <button class="btn_back" v-on:click="backButton()">{{ $t("back") }}</button>
</template>
<script>
  export default {
    name: 'button-back',
    data: function () {
      return {
      }
    },
    methods: {
      backButton() {
        window.history.back();
      }
    }
  }
</script>



