    <h6>Beeline tarmog’ida Wi-Fi dan foydalanish shartnomasi</h6>
    <p>Ushbu, Beeline tarmoqlarida Wi-Fi dan Foydalanish shartnomasi (keyingi o’rinlarda - Shartnoma), elektraloqa
      Operatori hisoblanuvchi (keyingi o’rinlarda – “Operator”), elektr aloqasi va ma’lumotlarni uzatish sohasida
      litsenziya asosida faoliyat yurituvchi, xususan, simsiz ulanish tarmog'idan foydalanish orqali Internetga kirish
      xizmatini taqdim etuvchi (keyingi o’rinlarda – “Xizmatlar”), 21.12.2016 yildagi 16-1526-KR Litsenziyasi asosida
      faoliyat olib boruvchi “Skay Mobayl” MChJ va identifikatsiyalash tartibidan o'tgan jismoniy shaxs (keyingi
      o’rinlarda – “Foydalanuvchi”) o’rtasidagi munosabatlarni belgilab boradi. Ushbu Shartnoma matni ochiq taklif
      hisoblanadi va identifikatsiya protsedurasidan o'tish taklif qabul qilinganini anglatadi, ya'ni foydalanuvchi
      ushbu Shartnomaning shartlariga to'liq va so’zsiz rozilik bildiradi.
    <p>1. Xizmatga kirish
    <p>1.1. Tarmoqqa kirish uchun foydalanuvchi o'zining foydalanish (oxirgi) moslamasini "Beeline" ning Wi-Fi
      tarmog'iga ulashi kerak.
    <p>1.2. Tarmoq tanlagandan so'ng avtorizatsiya oynasi paydo bo'ladi, foydalanuvchi bu yerga o’zi haqidagi
      ma'lumotlarni kiritishi kerak bo'ladi *:
    <p>1.2.1. Beeline telefon raqami;
    <p>1.2.2. Ushbu Shartnoma shartlari bilan tanishing va ularni qabul qilsangiz belgilab qo'ying;
      Shartnoma shartlari rad etilgan hollarda, "Beeline"ning Wi-Fi tarmog’idan foydalanish mumkin bo'lmaydi.
    <p>1.2.3. "Kod olish" tugmasini bosing. SMS-xabar orqali olingan kodni "Kodni kiriting" maydoniga kiriting. Keyin
      "Ulanish" tugmasini bosing;
    <p>1.2.4. Avtorizatsiya muvaffaqiyatli amalga oshirilganda, foydalanuvchiga tegishli xabar ko'rsatiladi va
      "Beeline"ning Wi-Fi tarmog’iga kirishga ruxsat etiladi. Avtorizatsiya muvaffaqiyatsiz amalga oshirilgan hollarda,
      foydalanuvchiga xatolar, uning sabablari va hal qilish usullari to’g’risidagi xabar ko’rsatiladi;
    <p>1.3. Xizmatga kirish vaqt va trafik hajmi bo’yicha cheklovlarsiz taqdim etiladi.
    <p>2. Xizmat ta'rifi
    <p>2.1. Ushbu xizmat Foydalanuvchilarga simsiz kirish tarmog'i orqali Internetga kirish imkonini beradi (keyingi
      o’rinlarda – “Xizmatlar”). Xizmat faqatgina Foydalanuvchi "Beeline" Wi-Fi tarmog’ining ruxsat etilgan kirish
      nuqtalarida radiosignal qat’iy qabul qilingan zonada joylashgan bo'lsa taqdim etiladi.
    <p>2.2. Foydalanuvchilarga xizmat ko’rsatish ushbu shartnoma shartlari bo'yicha taqdim etiladi.
    <p>2.3. Xizmatga kirish mobil telefon, shaxsiy planshetli kompyuterlar, noutbuklar va Wi-Fi texnologiyasi orqali
      tarmoqqa ulanish mumkin bo’lgan boshqa qurilmalardan foydalangan holda amalga oshirilishi mumkin. Xizmatlardan
      foydalanish uchun foydalanish (oxirgi) qurilmasini sozlash Foydalanuvchi tomonidan mustaqil amalga oshiriladi.
    <p>2.4. Xizmat zarur profilaktika va ta'mirlash ishlari o’tkazilishini mustasno qilganda, vaqt bo’yicha
      cheklovlarsiz taqdim etiladi.
    <p>2.5. Xizmatlar 2,4 GHz, 5 GHz chastotalar bo'yicha taqdim etilishi mumkin. Muayyan joyda xizmatni taqdim etish
      Operatorning texnik imkoniyatlari, shuningdek foydalanish (oxirgi) moslamaning texnik imkoniyatlariga bog'liq.
    <p>2.6. Xizmatlar 100 Mb / soniyali maksimal tezlikda taqdim etiladi. Tarmoq resurslaridan foydalanishning
      intensivlik darajasi va bitta kirish nuqtasiga ulangan barcha Foydalanuvchilarning bir vaqtning o'zida o'tkazib
      yuborilgan trafik hajmiga qarab, tezlik o'zgarishi mumkin. Operator foydalanish (oxirgi) qurilmasi va operatorning
      aloqa tarmog'i va Xizmatlarga uzluksiz kirish o'rtasida axborot almashish tezligiga kafolat bermaydi.
    <p>2.7. Mobil telefonning faqat bitta raqami yordamida bitta foydalanish (oxirgi) qurilmasini avtorizatsiya qilish
      mumkin.
    <p>2.8. Xizmatdan foydalanishda foydalanish (oxirgi) qurilmasiga dinamik, xususiy IP-manzillar tayinlanadi.
    <p>2.9. Xizmatlar, "xuddi shunday" ko’rinishda («as is») va taqdim etilgan paytda mavjud bo'lgan shaklda taqdim
      etiladi, bunda Foydalanuvchilarga axborot almashinuv tezligi, Xizmat va boshqalarga uzluksiz kirishning hech
      qanday, bevosita yoki bilvosita ko’rinishiga kafolat taqdim etilmaydi.
    <p>2.10. Operator, Qirg'iziston Respublikasi qonunchiligiga muvofiq tarqatish taqiqlangan axborotni o'z ichiga olgan
