      blokirovka qilingan axborotlardan tashqari har qanday Internet-resurslarga (tarmoqlar, serverlar, ma'lumotlar
      saqlanadigan joy va boshqalar) kirish huquqini cheklamaydi.
    <p>2.11. Foydalanuvchi ushbu Shartnomaning shartlarini qabul qilar ekan, Operator Xizmat ko’rsatishni amalga
      oshirayotganda Foydalanuvchining shaxsiy ma'lumotlarini qayta ishlash va Xizmatni ko'rsatish maqsadida ular bilan
