      bo'lgan Foydalanuvchi haqidagi ma'lumotlarni to'plash va qayta ishlash, Operator tomonidan ularga xizmat
      ko'rsatishda ma’lum bo’lgan ma’lumotlardan foydalanish, shuningdek Foydalanuvchining ehtiyojlari, imtiyozlari va
