      axborot xizmatlarini taqdim etish va uchinchi tomondan mobil ilovalar orqali SMS-xabar, elektron pochta, telefon,
      pochta, tarmoq orqali tarqatish va Operator tomonidan mustaqil belgilangan boshqa yo'llar bilan uchinchi
      shaxslarga faqat ushbu Foydalanuvchining yozma roziligi bilan topshiriladi, Qirg'iziston Respublikasi qonunchiligi
      va ushbu Shartnoma shartlarida nazarda tutilgan hollar bundan mustasno.

    <p>2.12. Ushbu Shartnomaning shartlarini qabul qilar ekan, Foydalanuvchi quyidagilarni tasdiqlaydi:
      - Xizmatdan foydalanish uchun o’z shaxsiy ma'lumotlarini taqdim etishda to'plangan ma'lumotlarning ro'yxati,
      2.11-bandda ko'rsatilgan maqsadlar uchun yig'ish va ulardan foydalanishga asoslangan maqsadlar bilan tanishadilar,
      shuningdek Qirg'iziston Respublikasi qonunchiligiga binoan o'z shaxsiy ma'lumotlarini vakolatli davlat organlariga
      o'tkazishlari mumkin;
      - Operator topshirig’i bilan faoliyat yurituvchi uchinchi shaxslarga o'z shaxsiy ma'lumotlarini qayta ishlash va
      Operator tomonidan xizmat ko'rsatish maqsadida ular bilan tanishish huquqiga ega ekanligiga rozilik beradi (erkin
      holda, o’z irodasi va manfaatlari bilan), Foydalanuvchi talablariga asosan aktualizatsiya/bloklash bundan
      mustasno. Shu bilan birga, abonent operator tomonidan ushbu Shartnoma nizomlarini tezda amalga oshirish uchun o'z
      shaxsiy ma'lumotlarini uchinchi shaxslar tomonidan elektron aloqa kanallari (Internet) orqali uzatilishiga rozilik
      bildiradi;
      - mazkur Shartnoma doirasida va uning amal qilish muddatida ma'lum bo'lgan uning shaxsiy ma'lumotlarini
      transchegaraviy uzatishga (erkin holda, o'z irodasi va manfaatlari bilan) o'z roziligini bildiradi.
    <p>2.13. Ushbu Shartnomada ko'zda tutilgan har qanday rozilikning muddati oferta - taklif qabul qilingan kundan
      boshlanadi va Foytdalanuvchi tomonidan Xizmatdan foydalanish muddati mobaynida va foydalanish to'xtatilgandan
      keyin 3 (uch) yil ichida davom etadi. Foydalanuvchi Operatorga yozma ariza bilan murojaat qilib har qanday
      rozilikni bekor qilish huquqiga ega.
    <p>3. Xizmatdan foydalanishda quyidagilar taqiqlanadi:
    <p>3.1. Xizmatdan foydalanishda boshqa Foydalanuvchilar kirishini cheklash yoki boshqa Foydalanuvchilarga to'siqlar
      qo’yish;
    <p>3.2. Spam, zararli dasturlarni (viruslarni) yuborish, elektron pochtaning ochiq retranslyatorlaridan foydalanish
      (open relays), adresat – xat oluvchining roziligisiz (yoki ariza yo'qligida) reklama, informatsion va boshqa
      materiallar, shuningdek, mavzu bo’yicha to’g’ri kelmaydigan elektron nashrlar va konferentsiyalarni yuborish;
    <p>3.3. Internet tarmoqlarida pornografik materiallar, zo'ravonlikka chaqirish, hokimiyatni ag'darish va
      Qirg’iziston Respublikasi hududida taqiqlangan boshqa axborotlarni tarqatish;
    <p>3.4. Operator va/yoki ushbu resurslar egalari tomonidan o'rnatilgan har qanday Internet-tarmoqlari resurslaridan
      foydalanish qoidalarini buzish va/yoki Operator resurslari, Internet foydalanuvchilari va Internet orqali kirish
      imkoniyatiga ega bo'lgan boshqa tarmoq resurslariga zarar yetkazish;
    <p>3.5. Har qanday texnologik komponentlar (tugunlar), dasturlar, ma'lumotlar bazasi va operator tarmog'ining boshqa
      komponentlariga ruxsatsiz kirib borish, shuningdek Operatorga, Internet va / yoki Internet foydalanuvchilari
      resurslariga zarar yetkazadigan har qanday boshqa xatti-harakatlarni amalga oshirish;
    <p>3.6. Xizmatlar orqali to'liq yoki qisman olingan ma'lumotlar, dasturiy ta'minot yoki boshqa materiallar, shu
      jumladan, mualliflik huquqi yoki boshqa intellektual huquqlar bilan himoyalangan mualliflik huquqi
      egasi/muallif/axborot yetkazib beruvchi/vakolatli vakilning roziligisiz yuborish, uzatish, ko'paytirish, taqdim
      etish yoki ulardan tijorat maqsadlarining har qanday shaklida foydalanish;
    <p>3.7. Xizmatga kirish uchun Foydalanuvchining avtomatik tarzda identifikatsiya qilinuvchi dasturiy ta’minotidan
      foydalanish;
    <p>3.8. Beeline ning Wi-Fi tarmoq'larida simsiz ishlash uchun uchinchi shaxslarga hisobga olinuvchi o’z shaxsiy
      ma'lumotlaringizni yuborish.
    <p>3.9. Foydalanish (oxirgi) qurilmasidan proksi-server, router va kompyuter orqali tarmoq ichida ulangan boshqa
      qurilmalar sifatida foydalanish.
    <p>3.10. Yuqorida ko’rsatib o’tilmagan va Internet tarmoqlari resurslaridan foydalanishning umumiy qoidalariga zid
      keladigan yoki Operator aloqa tarmog'ining yaxlitligiga tahdid soladigan boshqa harakatlarni amalga oshirish.
    <p>4. Operator quyidagilar uchun javobgar emas:
    <p>4.1. Xizmatdan foydalanish yoki undan foydalana olmaslik oqibatida Foydalanuvchiga bevosita yoki bilvosita
      yetkazilgan zarar;
    <p>4.2. Foydalanuvchining axborot va xizmatlarni Internet tarmoqlari orqali Xizmatlar vositasi bilan qabul qilishi
      natijasida bevosita yoki bilvosita kelib chiqadigan har qanday xarajatlar yoki zarar;
    <p>4.3. Internet tarmoqlari muayyan segmentlarining ishlashi va ularga kirish mumkinligi.
    <p>4.4. Operator Xizmatdan foydalanish vaqtida Qirg’iziston Respublikasi qonunchiligiga ko’ra uchinchi shaxslarning
      ayrim huquq va manfaatlari Foydalanuvchi tomonidan buzilganligi uchun javobgar bo'lmaydi.
    <p>5. Operator foydalanuvchilarning tarmoqqa ulanish va tarmoq trafigi yozuvlarini hisobga olishga haqlidir.
    <p>6. Operator Foydalanuvchi tomonidan ushbu Shartnomaning 3-bo’limida ko'rsatilgan shartlar buzilgan holda Operator
