    <p>6. Оператор Колдонуучу ушул Макулдашуунун 3-бөлүмүндө көрсөтүлгөн шарттарды бузган учурда Колдонуучуга Кызмат
      көрсөтүүнү кайсы гана учурда болбосун алдын ала эскертүүсүз токтотууга же чектөөгө укуктуу.
    <p>7. Beeline түйүнүндө Wi-Fi менен иштеген учурда көйгөйлөр келип чыкса, Колдонуучу Операторго 996771055155 номери
      боюнча WhatsApp же Telegram билдирүүсүн жазуу менен кайрыла алат.
    <p>8. Ушул Макулдашуу ал Оператордун Интернет түйүнүндөгү _______________________ дареги боюнча жайгашкан расмий
      сайтына жарыяланган күндөн тарта күчүнө кирет. Ушул макулдашуунун аракеттенүү мөөнөтү чектелген эмес.
    <p>9. Оператор ушул Макулдашууга бир тараптуу тартипте Макулдашуунун жаңы редакциясын Оператордун Интернет
      түйүнүндөгү _______________________ дареги боюнча жайгашкан расмий сайтына жарыялоо аркылуу кайсы гана болбосун
      өзгөртүүлөрдү/толуктоолорду киргизүүгө укуктуу. Мындай өзгөрүүлөр Макулдашуунун жаңы редакциясы жарыяланган күндөн
      тарта 10 календардык күн өткөндөн кийин күчүнө кирет. Колдонуучу ушул Макулдашуунун шарттарынын өзгөрүүсү менен
      эгерде Макулдашуу жаңы редакциясына киргенден кийин Кызматты пайдаланууну уланта турган болсо макул болот.
      Beeline түйүнүндөгү Wi-Fi ушул Колдонуучу Макулдашуусунун шарттарында Колдонуучунун интернет түйүнүнө кирүүсүг
      камсыздоого арналган.
      * - Колдонуучу Оператор тарабынан авторизациялоо процедурасынан өтүүгө, Колдонуучунун Оператордун тарифтик планды
      орнотуусу/тандоосуна ылайык акы алына тургандыгына макулдугун берет.
    </p>
  </div>
</template>
